.converterWrapper {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.currencyWrapper {
    background-color: #fff;
    margin: 0 20px;
    margin-bottom: -30px;
    padding: 36px 20px 70px;
    border-radius: 14px;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}

.syncWrapper {
    background-color: #67A3F6;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    height: 65px;
    width: 65px;
    border-radius: 50%;
    align-self: center;
    margin-top: -4px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.currencyTextField {
    padding: 7px;
    border: 2px solid black;
    border-radius: 9px;
    width: 100%;
    text-align: right;
}